@import "../../../assets/settings";

.reset-password {
  background-color: $main_auth_bg;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100%;
}

.redirect-link {
  text-decoration: none;
  color: #146BBC;
  margin-top: 12px;
}

.reset-password-form{
  display: flex;
  flex-direction: column;
  width: 335px;

  .input {
    margin-bottom: 10px;
  }

  .button {
    margin-bottom: 15px;
  }

}
